.recommend{
    padding-top:64px;
    padding-bottom:80px;
    &__title{
        font-size:48px;
        color:#650300;
        text-align:center;
        padding-bottom:60px;
    }
}
.recommend-list{
    display:flex;
    justify-content: space-between;
    &__item{
        width:23%;
        &:last-child{
            margin-right:0px;
        }
        .item-photo{
            display:flex;
            justify-content: center;
            width:255px;
            height:256px;
            overflow: hidden;
            &__img{
                display:block;
                height: 100%;
                margin:auto;
                object-fit: cover;
            }
        }
        .item-content{
            padding:14px 16px;
            border:1px solid #DEE2E6;
            border-top:none;
            &__name{
                display:block;
                font-size:14px;
                padding-bottom:7px;
            }
            &__text{
                font-size:16px;
                line-height: 24px;
                padding-bottom:10px;
            }
            &__time{
                font-size:14px;
                color:#707070;
            }
        }
    }
}

@media(max-width:992px){
    .recommend{
        padding-top:40px;
        padding-bottom:40px;
        &__title{
            font-size:32px;
            padding-bottom:32px;
        }       
    }
    .recommend-list{
        flex-wrap: wrap;
        &__item{
            width:45%;
            margin-bottom:28px;
            .item-photo{
                width:100%;
            }
        }
    }
}
@media(max-width:768px){
    .recommend{
        padding-top:32px;
        padding-bottom:32px;
        &__title{
            font-size:20px;
            padding-bottom:20px;
        }
    }
    .recommend-list{
        &__item{
            width:100%;
            .item-photo__img{
                width:100%;
                height:auto;
            }
            .item-content{
                padding:16px;
                &__name{
                    padding-bottom:16px;
                }

            }
            &:last-child{
                margin-bottom:0px;
            }
        }
    }
}