.product-title{
    padding-bottom:40px;
    text-align: center;
    &__en{
        font-size:42px;
        line-height: 63px;
    }
    &__text{
        font-size:32px;
    }
}
.product-list{
    display:flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    &__item{
        width:calc((100% - 90px) / 4);
        margin-bottom:104px;
        overflow: hidden;
        &:nth-last-child(-n+4){
            margin-bottom:100px;
        }
        .item-photo{
            display:flex;
            justify-content: center;
            max-width:100%;
            height:164px;
            &__img{
                display:block;
                width:100%;
                object-fit: cover;
            }
        }
        .item-info{
            display:flex;
            justify-content:space-between;
            align-items: center;
            &__number{
                font-size:24px;
            }
            &__price{
                font-size:24px;
                color:#AA0601;
                font-weight: 500;
                line-height: 36px;
            }
        }
        .item-style{
            display:flex;
            &__color{
                display:block;
                width:24px;
                height:24px;
                border-radius: 4px;
                margin-right:8px;
                &.brown{
                    background-color:#5F3E2D;
                }
                &.brown-light{
                    background-color:#B75929;
                }
                &:last-child{
                    margin-right:0px;
                }
                
            }
        }
    }
}

@media(max-width: 768px){
    .product-title{
        &__en{
            font-size:32px;
            line-height: 43px;
        }
        &__text{
            font-size:24px;
        }
    }
    .product-list{
        &__item{
            width:calc((100% - 30px) / 2);
            &:nth-last-child(-n+2){
                margin-bottom:104px;
            }
        }
    }
  
}

@media(max-width: 414px){
    .product-title{
        padding-bottom:20px;
        &__en{
            font-size:20px;
            line-height: 26px;
        }
        &__text{
            font-size:16px;
        }
    }
    .product-list{
        &__item{
            width:100%;
            margin-bottom:32px;
            &:nth-last-child(-n+4){
                margin-bottom:32px;
            }
            &:nth-last-child(-n+2){
                margin-bottom:32px;
            }
            &:nth-last-child(-n+1){
                margin-bottom:40px;
            }
            .item-info{
                &__number,
                &__price{
                    font-size:16px;
                    line-height:20px;
                }
            }
            .item-style__color{
                width:16px;
                height:16px;
                margin-right:4px;
                &:last-child{
                    margin-right:0px;
                }
            }
        }
    }
}