.header{
    display:flex;
    align-items: center;
    height:108px;
    padding:0px 88px;
    background-color:#AA0601;
    .logo{      
        display:block;  
        max-width:100%;
        &__icon{
            display:block;
            width:100%;
        }
    }
    .menu{
        display:flex;
        align-items: center;
        margin-left:auto;
        &__item{
            margin-right:64px;
            &:last-child{
                margin-right:0px;
            }
            .item-link{
                font-size:20px;
                color:white;
                text-decoration: none;
            }
        }
    }
}

@media(max-width:992px){
    .header{
        padding:0px 39px;
        .menu__item{
            margin-right:40px;
        }
      }
}
@media(max-width: 768px){
    .header{
        flex-direction: column;
        height:auto;
        padding:0px;
        .logo{
            display:flex;
            justify-content: center;
            align-items: center;
            flex-shrink: 0;
            width:100%;
            height:50px;
            border-bottom:1px solid white;
            &__icon{
                width:48px;
            }
        }
        .menu{
            width:100%;
            flex-wrap: wrap;
            margin-left:0px;
            &__item{
                display:flex;
                justify-content: center;
                align-items: center;
                flex-shrink: 0;
                width:50%;
                height:44px;
                margin-right:0px;
                outline:1px solid white;
            }
        }
        
    }
}