.tab-list{        
    display:flex;
    width:80%;
    margin:0 auto;
    &__item{
        position: relative;
        width:33.33333%;
        padding:24px 0px;
        border-left:1px solid #DCDCDC;
        .item-link{
            display:flex;
            align-items: center;
            justify-content: center;
            height:100%;
            &__icon{
                max-width: 100%;
                height:38px;
                margin-right:8px;
            }
            &__text{
                font-size:42px;
                color:black;
            }
        }
        
        &:last-child{
            border-right:1px solid #DCDCDC;
        }
        &--active{
            &:before{
                content:'';
                position: absolute;
                left:0px;
                bottom:0;
                width:100%;
                height:16px;
                background-color:#AA0601;
            }
        }
    }
}



@media(max-width: 768px){
    .menu-tab{
        height:69px;
    }
    .tab-list{
        width:100%;
    }
    .tab-list__item{
        .item-link{
            &__icon{
                height:32px;
            }
            &__text{
                font-size:28px;
            }
        }       
        &--active{
            &:before{
                height:8px;
            }
        }       
    } 
}

@media(max-width: 414px){
    .menu-tab{
        height:35px;
    }
    .tab-list__item{
        .item-link{
            &__icon{
                height:14px;
                margin-right:4px;
            }
            &__text{
                font-size:14px;
            }          
        }
        &--active{
            &:before{
                height:4px;
            }
        }
    }
    
}