.banner{
    display:flex;
    justify-content: space-between;
    margin-bottom:88px;
    &__item{
        flex:1;
        height:309px;
        margin-right:30px;
        background-size:cover;
        background-position: center;
        background-repeat: no-repeat;
        &:first-child{
            background-image: url('https://hexschool.github.io/webLayoutTraining1st/week3/banner-1.png');
        }
        &:last-child{
            background-image: url('https://hexschool.github.io/webLayoutTraining1st/week3/banner-2.png');
            margin-right:0px;
        }
        .item-photo{
            display:block;
            width:100%;
            height:auto;
        }
    }
}
.banner-full{
    height:840px;
    background-size:cover;
    background-position: center;
    background-repeat: no-repeat;
    background-image: url('https://hexschool.github.io/webLayoutTraining1st/week3/banner.png');
    .container{
        justify-content: center;
        align-items: flex-end;
        height:100%;
    }
    .banner-dec{
        font-size:36px;
        color:#650300;
    }
    .banner-slogan{
        font-size:64px;
        color:#650300;
        line-height: 96px;
    }
}
@media(max-width:992px){
    .banner-full{
        .banner-dec{
            font-size:20px;
        }
        .banner-slogan{
            font-size:48px;
        }
    }
    
}
@media(max-width: 768px){
    .banner{
        margin-bottom:40px;
        &__item{
            margin-right:0px;
        }
    }
  
}

@media(max-width: 414px){
    .banner{
        margin-bottom:30px;
        &__item{
            height:145px;
            margin-right:0px;
        }
    }
    .banner-full{
        height:394px;
        .banner-dec{
            font-size:16px;
        }
        .banner-slogan{
            font-size:32px;
            line-height: 72px;
        }
    }
}