.about{
    background-color:#AA06010D;
    padding-top:64px;
    padding-bottom:80px;
    &__title{
        font-size:48px;
        color:#650300;
        text-align:center;
        padding-bottom:60px;
    }
    &__list{
        display:flex;
        justify-content: space-between;
        .list-item{
            width:20%;
            &__icon{
                display:flex;
                height:100px;
                margin-bottom:40px;
                .icon-img{
                    display: block;
                    max-width: 100%;
                    height: 100%;
                    margin:auto;
                }
                
            }
            &__title{
                font-size:24px;
                color:#650300;
                line-height:36px;
                padding-bottom:4px;
                text-align: center;
            }
            &__text{
                font-size:16px;
            }
        }
    }
}

@media (max-width: 992px){
    .about{
        padding-top:40px;
        &__title{
            font-size:32px;
            padding-bottom:32px;
        }
        &__list{
            flex-wrap: wrap;
            justify-content: space-around;
            .list-item{
                width:35%;
                margin-bottom:40px;
                &__icon{
                    height:80px;
                    margin-bottom:16px;
                }
                &__title{
                    font-size:20px;
                    padding-bottom:8px;
                }
                &__text{
                    font-weight:600;
                    line-height:24px;
                }
            }
        }
    }
    
}

@media(max-width: 768px){
    .about{
        padding-top:32px;
        &__title{
            font-size:20px;
            padding-bottom:24px;
        }
        &__list{
            .list-item{
                width:70%;
                &__title{
                    line-height:30px;
                }
            }
        }
    }
  
}