.footer{
    background-color:#AA0601;
    position: relative;
}
.footer-link{
    display:flex;
    justify-content: space-between;
    align-items:center;
    padding-top:36px;
    .menu{
        display:flex;
        &__item{
            padding-right:48px;
            &:last-child{
                padding-right:0px;
            }
            .item-text{
                font-size:16px;
                color:white;
            }
        }
    }
}
.sns{
    display:flex;
    &__icon{
        max-width:100%;
        height:auto;
        margin-right:32px;
        &:last-child{
            margin-right:0px;
        }
        .icon-img{
            display:block;
            width:100%;
        }
    }
}
.sns-mobile{
    display:none;
    position: absolute;
    top:24px;
    right:15px;   
    &__icon{
        width:24px;
        height:auto;
        margin-right:24px;
        &:last-child{
            margin-right:0px;
        }
        .icon-img{
            display:block;
            width:100%;
        }
    }
}
.footer-logo{
    display:none;
    position: absolute;
    bottom:21px;
    right:15px;
    &__icon{
        display:block;
        width:46px;
    }
}
.footer-contact{
    padding-top:18px;
    padding-bottom:24px;
    border-bottom:1px solid white;
    &__item{
        display:flex;
        align-items: center;
        margin-bottom:26px;
        &:last-child{
            margin-bottom:0px;
        }
        .item-icon{
            max-width:100%;
            height:auto;
            margin-right:14px;
        }
        .item-text{
            font-size:32px;
            color:white;
            text-decoration: none;
        }
    }
}
.footer-copyright{
    display:flex;
    justify-content: space-between;
    align-items: center;
    padding-top:24px;
    padding-bottom:28px;
    .copyright{
        display:flex;
        align-items: center;
        &__icon{
            max-width:100%;
            height:auto;
            margin-right:38px;
        }
        &__text{
            font-size:16px;
            color: white;
        }
    }
    .terms{
        display:flex;
        align-items: center;
        &__item{
            margin-right:62px;
            &:last-child{
                margin-right:0px;
            }
            .item-text{
                font-size:14px;
                color:white;
                text-decoration: none;
            }
        }
    }
}

@media(max-width: 768px){
    .footer-link{
        padding-top:40px;
        .menu__item{
            padding-right:40px;
        }
        .sns__icon{
            width:24px;
            margin-right:24px;
            &:last-child{
                margin-right:0px;
            }
        }
    }
    .footer-contact{
        &__item{
            margin-bottom:12px;
            .item-icon{
                width:28px;
                margin-right:10px;
            }
            .item-text{
                font-size:20px;
            }
        }   
    }
    .footer-copyright{
        padding-top:25px;
        padding-bottom:40px;
        .copyright__icon{
            display:none;
        }
        .terms__item{
            margin-right:24px;
            &:last-child{
                margin-right:0px;
            }
        }
    }
  
}

@media(max-width: 414px){
    .footer-link{
        display:none;
    }
    .footer-contact{
        padding-top:24px;
        .item-icon{
            width:16px;
            margin-right:6px;
        }
        .item-text{
            font-size:16px;
        }
    }
    .sns-mobile{
        display:flex;
    }
    .footer-logo{
        display:block;
    }
    .footer-copyright{
        flex-direction: column;
        align-items: flex-start;
        padding-top:18px;
        padding-bottom:25px;
        .copyright__text{
            font-size:14px;
        }
        .terms{
            flex-direction: column;
            align-items: flex-start;
            &__item{
                padding-top:8px;
            }
        }
    }
    
}