.contact{
    padding-top:64px;
    padding-bottom:80px;
    background-color:#AA06010D;
    &__title{
        font-size:48px;
        text-align:center;
        padding-bottom:60px;
    }
    &__form{
        width:65%;
        margin:0 auto;
        .form-info{
            font-size:20px;
            line-height: 30px;
            padding-bottom:20px;
            margin:0px; 
        }
        .form-col{
            display:flex;
            flex-direction: column;
            padding-bottom:16px;
            &__title{
                font-size:16px;
                padding-bottom:4px;
            }
            &__input{
                display:block;
                font-size:16px;
                padding:14px 16px;
                border:1px solid black;
            }
            &__textarea{
                min-height:136px;
                padding:14px 16px;
                font-size:16px;
                resize: none; 
                border:1px solid black;
            }
           
        }
        .form-row{
            display:flex;
            &__check{
                width:20px;
                height:20px;
                border-radius: 4px;
                border:1px solid black;
                background-color:transparent;
            }
            &__label{
                display:inline-block;
                padding-left:4px;
                font-size:16px;
                line-height: 24px;
            }
        }
        .form-btn{
            display:flex;
            justify-content: center;
            align-items: center;
            width:60%;
            height:54px;
            background-color:#AA0601;
            font-size:16px;
            color:white;
            margin:auto;
            margin-top:40px;
            &:hover{
                background-color:rgba(#AA0601,0.8);
                color:black;
            }
        }
    }
}

@media(max-width:992px){
    .contact{
        padding-top:40px;
        padding-bottom:40px;
        &__title{
            font-size:32px;
            padding-bottom:16px;
        }   
        &__form{
            .form-info{
                font-size:16px;
                line-height:24px;
                padding-bottom:12px;
            }
            .form-col{
                padding-bottom:8px;
                &__title{
                    font-size:14px;
                }
                &__input{
                    padding:8px 12px;
                    font-size:14px;
                }
            }
            .form-row__label{
                font-size:14px;
                line-height:21px;
            }
            .form-btn{
                height:36px;
                font-size:14px;
                margin-top:20px;
            }
        }    
    }
}
@media(max-width:768px){
    .contact{
        padding-top:32px;
        padding-bottom:32px;
        &__title{
            font-size:20px;
            padding-bottom:20px;
        }
        &__form{
            width:100%;
        }
    }
}