.catalog-classic{
    padding-top:64px;
    padding-bottom:80px;
    &__title{
        font-size:48px;
        color:#650300;
        text-align:center;
        padding-bottom:60px;
    }
}
.catalog-classic-list{
    &__item{
        margin-bottom:32px;
        &:last-child{
            margin-bottom:0px;
        }
        &:nth-child(even){
            .item-link__photo{
                margin-right:0px;
                margin-left:30px;
            }
            .item-link__title{
                justify-content: flex-end;
            }
        }
    }
    .item-link{
        display:flex;
        align-items:center;
        &__photo{
            position: relative;
            display:flex;
            justify-content: center;
            align-items: center;
            width:60%;
            margin-right:30px;
            .photo-img{
                display:block;
                max-width:100%;
            }
            .photo-mobile{
                position:absolute;
                align-items: center;
                display:none;
                &__icon{
                    display:block;
                    width:32px;
                }
                &__title{
                    font-size:28px;
                    line-height:42px;
                    font-weight: 600;
                    font-style: italic;
                    color:white;
                }
            }
        }
        &__title{
            display:flex;
            align-items: center;
            width:40%;
            .title-icon{
                display:block;
                max-width:100%;
                margin-right:8px;
            }
            .title-text{
                font-size:48px;
                color:#AA0601;
                line-height: 63px;
                font-style: italic;
                font-family: Roboto,sans-serif;
                font-weight: 700;
            }
        }        
    }
}
.catalog-com{
    padding-top:64px;
    padding-bottom:80px;
    background-image: url('https://hexschool.github.io/webLayoutTraining1st/week3/bg.png');
    background-size:cover;
    background-position: center;
    background-repeat: no-repeat;
    &__title{
        font-size:48px;
        color:white;
        text-align:center;
        padding-bottom:60px;
    }
}
.catalog-com-list{
    display:flex;
    &__item{
        width: 50%;
        height:472px;
        margin-right:30px;
        &:last-child{
            margin-right:0px;
        }
        .item-link{
            display:flex;
            height:100%;
            background-color:rgba(255,255,255,0.4);
            &__title{
                font-size:72px;
                color:#AA0601;
                margin:auto;
            }
            &:hover{
                background-color:transparent;
            }
        }
        &:first-child{
            background-image: url('https://hexschool.github.io/webLayoutTraining1st/week3/product-8.png');
            background-size:cover;
            background-position: center;
            background-repeat: no-repeat;         
        }
        &:last-child{
            background-image: url('https://hexschool.github.io/webLayoutTraining1st/week3/product-9.png');
            background-size:cover;
            background-position: center;
            background-repeat: no-repeat;            
        }
    }
}


@media(max-width:992px){
    .catalog-classic,
    .catalog-com{
        padding-top:40px;
        padding-bottom:40px;
        &__title{
            font-size:32px;
            padding-bottom:32px;
        }       
    }
    .catalog-classic-list{
        &__item{    
            margin-bottom:8px;       
            &:nth-child(2n){
                .item-link{
                    flex-direction: row-reverse;
                    &__photo{
                        margin-right: 30px;
                        margin-left:0px;
                    }
                    &__title{
                        justify-content: flex-start;
                    }
                }
                
            }
            .item-link{              
                &__photo{
                    width:60%;

                }
                &__title{
                    .title-icon{
                        width:32px;
                    }
                    .title-text{
                        font-size:28px;
                    }
                }
            }
        }
    }
    .catalog-com-list{
        &__item{
            height:280px;
            .item-link__title{
                font-size:32px;
            }
        }
    }
}

@media(max-width:768px){
    .catalog-classic,
    .catalog-com{
        padding-top:32px;
        padding-bottom:32px;
        &__title{
            font-size:20px;
            padding-bottom:20px;
        }
    }
    .catalog-classic-list{
        &__item{
            &:nth-child(2n){
                .item-link{
                    &__photo{
                        margin-right: 0px;
                    }
                }
                
            }
            .item-link__photo{
                width:100%;
                margin-right:0px;
                .photo-mobile{
                    display:flex;
                }
            }
            .item-link__title{
                display:none;
            }
        }
    }
    .catalog-com-list{
        flex-wrap: wrap;
        &__item{
            width:100%;
            margin-right:0px;
            margin-bottom:16px;
            &:last-child{
                margin-bottom:0px;
            }
        }
    }
}