.layout-content {
  width:100%;
  margin: 0 auto;
}
.container{
  display:flex;
  flex-direction: column;
  max-width:1100px;
  margin:0 auto;
}
@media (max-width: 992px){
 .container{
   max-width:690px;
 }
  
}
@media(max-width: 768px){
  .container{
    padding:0px 39px;
  }

}
@media(max-width: 414px){
  .container{
    max-width: 345px;
  }
}