.pages{
    display:flex;
    justify-content: center;
    padding-bottom:156px;
    &__item{
        display:flex;
        min-width:15px;
        height:48px;
        padding:0px 12px;
        border:1px solid #555555;
        .item-text{
            display:block;
            font-size:18px;
            color:#555555;
            margin:auto;
        }
        &--active{
            background-color:black;
            .item-text{
                color:white;
            }
        }
    }
}

@media(max-width: 768px){
    .pages{
        padding-bottom:104px;
    }
  
}

@media(max-width: 414px){
    .pages{
        padding-bottom:56px;
        &__item{
            height:32px;
            min-width: 10px;
            padding: 0px 8px;
            .item-text{
                font-size:12px;
            }
        }
    }
}